@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

body{
    font-family: 'Poppins', sans-serif;
    color:#fff;
    line-height: 1.7;
}

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    box-sizing: border-box;
    text-decoration: none;
}

:root {
   --color-bg: #1f1f38
}




.wrapper {
    display: grid;
    grid-template-areas:
            'h h'
            'n c';
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    margin:10px auto;
    width: 1200px;
    grid-gap: 10px;

}

.wrapper-content{
    grid-area: c;
    /*background-image: url("../src/assets/images/slider-bg-01.jpg");*/
    background-color: #000;
    background-size: cover;
}





